import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
export default {
	components: {
		uniNavBar
	},
	data() {
		return {
			count:1,
			datas:[],
			navBarTitle: '点检标准项次',
			leftIcon: 'iconfanhui',
			rightIcon: 'iconchaxun',
			exitStr: '',
		}
	},
	onLoad(option) {
		this.datas.push(JSON.parse(option.item))
	},
	onShow() {
		
	},
	methods: {
		toDetail(item){
			//跳转点检标准项次详情页面
			uni.navigateTo({
				url:'xiangciDetails?item='+JSON.stringify(item)
			})
		},
		leftNavTop: () => {
			uni.navigateBack()
		},
		rightNavTop: () => {
			//跳转点检标准项次查询页面
			uni.navigateTo({
				url: '/pages/dj/dianjianbiaozhun/xiangciSearch'
			})
		}
	}
}
